* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  border: 1px solid #140000;
  border-radius: 10px;
  display: block;
}

input {
  width: 500px;
  height: 50px;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 5px 15px;
  font-size: 14px;
}

button {
  width: 100px;
  height: 25px;
  cursor: pointer;
  background-color: #deb887;
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  display: flex;
}

button:hover, button:focus {
  background-color: #fe84f4;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load-more {
  width: 150px;
  background-color: #deb887;
  margin: 40px auto;
}

.load-more:hover, .load-more:focus {
  background-color: #fe84f4;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 100px;
  display: flex;
}

.photo-card {
  background-color: rgba(249, 246, 175, .886);
  border: 1px solid #140000;
  border-radius: 10px;
  padding: 15px;
}

.photo-wrap {
  height: 250px;
  align-items: center;
  display: flex;
}

.gallery-link {
  text-decoration: none;
  display: block;
}

